import { AxisPosition, Dimension } from '../axis.types';
import { AXIS_DEFAULTS } from './axisDefaults';
import type { useTextDimensions } from '../../hooks/PRIVATE_useTextDimensions';
type GetTextDimensions = NonNullable<ReturnType<typeof useTextDimensions>['getTextDimensions']>;

/**
 * Returns the (x, y) coordinate of the text given the width and height of the chart.
 * @param position
 * @param availSpace
 * @param textDims
 * @returns
 */
export const getTitlePos = (
  position: AxisPosition,
  availSpace: Dimension,
  isRtl: boolean,
  textDims: { width: number; height: number }
) => {
  let x: number;
  let y: number;

  if (position === 'left') {
    x = availSpace.x + textDims.height / 2 - (isRtl ? textDims.height / 2 : 0);
    y = availSpace.y + availSpace.height / 2;
  } else if (position === 'right') {
    x = availSpace.x + availSpace.width - (isRtl ? textDims.height / 2 : 0);
    y = availSpace.y + availSpace.height / 2;
  } else if (position === 'top') {
    x = availSpace.x + availSpace.width / 2;
    y = availSpace.y + textDims.height / 2;
  } else {
    x = availSpace.x + availSpace.width / 2;
    y = availSpace.y + availSpace.height - textDims.height / 2;
  }
  return { x, y };
};

/**
 * Returns the title props.
 * @param titleStyle
 * @param availSpace
 * @param dims
 * @param position
 * @returns
 */
export function getTitleProps(
  title: string | undefined,
  titleStyle: Record<string, any>,
  maxAxisDims: number,
  availSpace: Dimension,
  position: AxisPosition,
  isRtl: boolean,
  getTextDimensions?: GetTextDimensions
) {
  if (!title || !getTextDimensions) {
    return { width: 0, height: 0 };
  }

  const isHoriz = position === 'top' || position === 'bottom';
  const { width, height } = getTextDimensions(title, titleStyle);

  const titleWidth = isHoriz ? width : height + AXIS_DEFAULTS.titleGap;
  const titleHeight = isHoriz ? height + AXIS_DEFAULTS.titleGap : width;

  if (
    (!isHoriz && titleWidth > maxAxisDims) ||
    (isHoriz && titleHeight > maxAxisDims) ||
    width === 0 ||
    height === 0
  ) {
    // title does not fit or not rendered
    return { width: 0, height: 0 };
  }

  const { x, y } = getTitlePos(position, availSpace, isRtl, { width, height });
  const isRotated = position === 'left' || position === 'right';
  const transform = isRotated
    ? `matrix(0, ${isRtl ? 1 : -1}, ${isRtl ? -1 : 1}, 0, ${x}, ${y})`
    : undefined;
  const titleDims = AXIS_DEFAULTS.titleGap + height;

  if (isRotated) {
    availSpace.width -= titleDims;
    availSpace.x += position === 'left' ? titleDims : 0;
  } else {
    availSpace.height -= titleDims;
    availSpace.y += position === 'top' ? titleDims : 0;
  }
  return {
    width,
    height: titleDims,
    x: isRotated ? undefined : x,
    y: isRotated ? undefined : y,
    transform: isRotated ? transform : undefined,
    style: { fill: titleStyle['color'], ...titleStyle }
  };
}
